/* Yandex Share */
.content ul.ya-share2__list {
  margin: 0;
}
.btn-share {
  position: relative;
  display: block;
}
.share-block__show {
  position: absolute;
  top: 55px;
  left: -15px;
  display: none;
  width: 250px;
  background: #fff;
  border-radius: 2px;
  padding: 1.5rem 1rem;
  -webkit-box-shadow: 0 2px 25px #76767626;
  box-shadow: 0 2px 25px #76767626;
  font-size: 14px;
  line-height: 20px;
  color: #7C8994;
  z-index: 99;
}
.share-block__show::after {
  content: "";
  width: 16px;
  height: 16px;
  background: #fff;
  border-radius: 2px;
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
  position: absolute;
  top: -8px;
  left: 80px;
  z-index: 0;
}
.ya-share2__badge {
  width: 32px !important;
  height: 32px !important;
  border-radius: 50% !important;
  display: flex !important;
  justify-content: center;
  align-items: center;
}
.content ul li.ya-share2__item::after {
  background: none;
}



